/* Footer.css */
.contact-form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-right: 2rem;
}

.contact-form>h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}

.contact-form form {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    font-size: 16px;
}

.contact-form p{
    color: white;
}

.contact-form input {
    width: 100%;
    height: 35px;
    padding: 7px;
    outline: none;
    border-radius: 5px;
    border: 1px solid rgb(220, 220, 220);
}

.contact-form input:focus {
    border: 2px solid rgba(0, 206, 158, 1);
}

.contact-form textarea {
    max-width: 100%;
    min-width: 100%;
    width: 100%;
    max-height: 100px;
    min-height: 100px;
    padding: 7px;
    outline: none;
    border-radius: 5px;
    border: 1px solid rgb(220, 220, 220);
}

.contact-form textarea:focus {
    border: 2px solid rgba(0, 206, 158, 1);
}

.contact-form label {
    margin-top: 1rem;
}

.contact-form input[type="submit"] {
    margin-top: 2rem;
    cursor: pointer;
    background: rgb(249, 105, 14);
    color: white;
    border: none;
}


@media only screen and (min-width:850px)
{
    .contact-form form {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        width: 50%;
        font-size: 16px;
    }
}